<template>
  <div class="team">
    team pages
    <ul>
    	<router-link  v-for="(item, index) in links" :to="item.path" tag="li" v-text="item.title" key="index"></router-link>
    </ul>
     <router-view ></router-view>
  </div>
</template>

<script>
  export default {
  	data() {
  		return {
  			links: [{title:'积分榜',path: '/team/score'}, {title: '射手榜', path: '/team/shooter'}, {title: '助攻榜', path: '/team/assist'}, {title: '赛程榜', path: '/team/schedule'}]
  		}
  	}
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.team
  ul
    width: 100%
    height: 36px
    line-height: 36px
    display: flex
    justify-content: center
    font-size: 1rem
    li
      flex: 1
      border-bottom: 1px solid $color-border
    .router-link-active
      color: $color-g
      border-bottom: 1px solid $color-g
</style>


